import React from "react";
import PropTypes from "prop-types";
import "./css/Movie.css";

function Movie({ poster, title, summary, year, rating, genres, index }) {
    return <div className="movie--container">
        <div className="movie--data">
            <img src={poster} alt={title} className="movie--poster"></img>
            <div className="movie--information">
                <h3 className="movie--title">{title}</h3>
                <h6 className="movie--year">{year}</h6>
                <ul className="movie--genres">{genres.map((genre, index) => (
                    <li key={index} className="movie--genre">{genre}</li>
                    ))}
                </ul>
                <h6 className="movie--rating">{rating}</h6>
                {/* <h4>{genres.map(genre => <h1>{genre}</h1>)}</h4> */}

            </div>
        </div>
        <p className="movie--summary">{summary.slice(0,250)}...</p>
    </div>
}

Movie.propTypes = {
    // id: PropTypes.number.isRequired,
    // key: PropTypes.number.isRequired,
    poster: PropTypes.string,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    year: PropTypes.number.isRequired,
    rating: PropTypes.number.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
}
Movie.defaultProps = {
    genres: []
}

export default Movie;